<template>
  <div class="multiple">
    <div class="content">
      <div class="title_text">
        <div class="title">{{ detail.name }}</div>
        <div class="num">
          <span>{{ active + 1 }}</span
          >/{{ mulList.length }}
        </div>
      </div>
      <div>
        {{ detail.topic }}
      </div>
      <div class="choice" :class="{ answer: detail.ischecked }">
        <p
          v-for="(item, index) in detail.arr"
          :key="index"
          @click="AddCLick(item)"
          :class="{
            active: detail.value == item.value,
            success: detail.answer == item.value && detail.ischecked,
            error:
              item.value == detail.value &&
              detail.answer !== item.value &&
              detail.ischecked,
          }"
        >
          {{ tag[index] }}、{{ item.name }}
        </p>
      </div>
      <div v-if="detail.ischecked" class="correct">
        正确答案：<span>{{ detail.answer }}</span>
      </div>
    </div>
    <div class="bom_btn" :class="{ disabled: isnext }" @click="NextClick">下一题</div>
    <!-- <div class="bom_btn" v-if="active != 0" @click="preClick">上一题</div> -->
    <!-- <div class="bom_btn" v-else @click="save">完成</div> -->
  </div>
</template>
<script>
export default {
  data() {
    return {
      active: 0,
      tag: ["A", "B", "C", "D", "E", "F", "G"],
      mulList: [
        {
          type: 1,
          name: "单选题",
          value: null,
          ischecked: false,
          answer: "A",
          topic: "1.回复12345政务热线工单的处理结果包含以下几项内容_____。(单选题)",
          arr: [
            { name: "退款金额", value: "A" },
            { name: "用户满意度", value: "B" },
            { name: "故障修复报告", value: "C" },
            { name: "回访用户时间", value: "D" },
          ],
        },
        {
          type: 2,
          name: "判断题",
          value: null,
          ischecked: false,
          answer: "B",
          topic: "1.回复12345政务热线工单的处理结果包含以下几项内容_____。(判断题)",
          arr: [
            { name: "正确", value: "A" },
            { name: "错误", value: "B" },
          ],
        },
      ],
    };
  },
  computed: {
    detail() {
      return this.mulList[this.active];
    },
    isnext() {
      return !this.mulList[this.active].value;
    },
  },
  methods: {
    AddCLick(item) {
      if (this.detail.ischecked) return;
      if (this.mulList[this.active].value == item.value) {
        this.mulList[this.active].value = null;
      } else {
        this.mulList[this.active].value = item.value;
      }
    },
    preClick() {
      this.active -= 1;
    },
    NextClick() {
      if (this.isnext) {
        return false;
      } else {
        if (this.detail.ischecked) {
          this.active += 1;
        } else {
          this.detail.ischecked = true;
        }
      }
    },
    save() {},
  },
};
</script>
<style lang="scss" scoped>
.multiple {
  .content {
    margin: 0 auto;
    width: 90%;
    border-radius: 5px;
    background-color: #fff;
    padding: 1rem;
    height: 33.125rem;
    // height: calc(100% - 30px);
    .title_text {
      display: flex;
      justify-content: space-between;
      .title {
        background-image: url("../assets/image/title_icon.png");
        width: 122px;
        height: 2.7rem;
        background-position: center;
        background-size: cover;
        color: #fff;
        text-align: center;
        line-height: 2.7rem;
        position: relative;
        top: -1.7rem;
        left: -1rem;
      }
      .num {
        color: #979797;
        span {
          color: #d42454;
        }
      }
    }
    .choice {
      margin-top: 1.9rem;
      p {
        width: 100%;
        border: 1px solid #979797;
        border-radius: 5px;
        height: 3.2rem;
        line-height: 3.2rem;
        padding-left: 0.7rem;
        margin-bottom: 1.25rem;
      }
      .active {
        color: #fff;
        background-color: #f1727c;
        border: 0;
      }
      .success {
        color: #00862a;
        background-color: #fff;
        border: 1px solid #00862a;
        position: relative;
      }
      .success::before {
        content: "";
        background: url("../assets/image/success_icon.png") no-repeat;
        position: absolute;
        display: inline-block;
        height: 1rem;
        width: 1rem;
        top: 57%;
        transform: translateY(-50%);
        background-color: #fff;
        padding: 0.1875rem;
      }
      .error {
        border: 1px solid #d81e06;
        color: red;
        background-color: #fff;
        position: relative;
      }
      .error::before {
        content: "";
        background: url("../assets/image/error_icon.png") no-repeat;
        position: absolute;
        display: inline-block;
        height: 1rem;
        width: 1rem;
        top: 57%;
        transform: translateY(-50%);
        background-color: #fff;
        padding: 0.1875rem;
      }
    }
    .correct {
      height: 2.1875rem;
      background: #f2f2f7;
      border-radius: 0.375rem;
      line-height: 2.1875rem;
      padding-left: 0.5625rem;
      color: #333333;
      span {
        color: #00862a;
      }
    }
  }
  .bom_btn {
    display: block;
    height: 3.2rem;
    width: 80%;
    border-radius: 5px;
    color: #fff;
    font-size: 20px;
    line-height: 50px;
    text-align: center;
    background: #d42454;
    margin: 2.8rem auto 0rem;
  }
  .disabled {
    background-color: #efafc0;
  }
}
</style>
