<template>
  <div class="multiple">
    <div class="content">
      <div class="title_text">
        <div class="title">视频题</div>
        <div class="num">
          <span>{{ active + 1 }}</span
          >/{{ mulList.length }}
        </div>
      </div>
      <div>{{ detail.topic }}</div>
      <div class="midd_box" v-if="show">
        <div class="title_name">3、请观看视频回答问题</div>
        <div><img src="../assets/image/1.video.png" alt="" /></div>
      </div>

      <div v-else class="choice">
        <p
          v-for="(item, index) in detail.arr"
          :key="index"
          @click="BtnNext(item)"
          :class="{
            active: detail.value == item.value,
            success: detail.answer == item.value && detail.ischecked,
            error:
              item.value == detail.value &&
              detail.answer !== item.value &&
              detail.ischecked,
            actives: item.checked,
          }"
        >
          {{ tag[index] }}、{{ item.name }}
        </p>
      </div>
    </div>
    <div>
      <div class="bom_btn" @click="ClickAnswer" v-if="show">点击回答问题</div>
      <div
        class="nx_btn bom_btn"
        :class="{ disabled: Isnext }"
        @click="NextClick"
        v-else
        v-show="active != mulList.length - 1"
      >
        下一题
      </div>
      <!-- v-show="active != mulList.length - 1" -->
      <div
        class="bom_btn"
        v-if="active == mulList.length - 1"
        :class="{ disabled: Isnext }"
        @click="acClick"
      >
        完成
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      show: true,
      active: 0,
      tag: ["A", "B", "C", "D", "E", "F", "G"],
      mulList: [
        {
          type: 1,
          name: "单选题",
          value: null,
          ischecked: false,
          answer: "A",
          topic:
            "1.回复12345政务热线工单的处理结果包含以下几项内容_____。(单选题)",
          arr: [
            { name: "退款金额", value: "A" },
            { name: "用户满意度", value: "B" },
            { name: "故障修复报告", value: "C" },
            { name: "回访用户时间", value: "D" },
          ],
        },
        {
          type: 2,
          name: "单选题",
          value: null,
          ischecked: false,
          answer: "B",
          topic:
            "1.回复12345政务热线工单的处理结果包含以下几项内容_____。(判断题)",
          arr: [
            { name: "正确", value: "A" },
            { name: "错误", value: "B" },
          ],
        },
        {
          type: 3,
          name: "多选题",
          value: null,
          ischecked: false,
          answer: "B",
          topic:
            "1.回复12345政务热线工单的处理结果包含以下几项内容_____。(多选题)",
          arr: [
            { name: "正确", value: "A" },
            { name: "错误", value: "B" },
          ],
        },
      ],
    };
  },
  methods: {
    ClickAnswer() {
      this.show = false;
    },
    BtnNext(item) {
      if (this.detail.ischecked) return;
      if (this.detail.value == item.value) {
        this.detail.value == null;
      } else {
        this.detail.value = item.value;
        console.log((this.detail.value = item.value));
      }
    },
    NextClick() {
      if (this.Isnext) {
        return false;
      } else {
        if (this.detail.ischecked) {
          this.active += 1;
        } else {
          this.detail.ischecked = true;
          console.log((this.detail.ischecked = true));
        }
      }
    },
    acClick() {
      this.detail.ischecked = true;
      if (this.Isnext) {
        return false;
      }
    },
  },
  computed: {
    Isnext() {
      return !this.mulList[this.active].value;
    },
    detail() {
      return this.mulList[this.active];
    },
  },
};
</script>
<style lang="scss" scoped>
.multiple {
  .content {
    margin: 0 auto;
    width: 90%;
    border-radius: 5px;
    background-color: #fff;
    padding: 1rem;
    height: 33.125rem;
    .title_text {
      display: flex;
      justify-content: space-between;
      .title {
        background-image: url("../assets/image/title_icon.png");
        width: 122px;
        height: 2.7rem;
        background-position: center;
        background-size: cover;
        color: #fff;
        text-align: center;
        line-height: 2.7rem;
        position: relative;
        top: -1.7rem;
        left: -1rem;
      }
      .num {
        color: #979797;
        span {
          color: #d42454;
        }
      }
    }
    .midd_box {
      .title_name {
        margin-bottom: 17px;
        color: #333333;
      }
    }
    .choice {
      margin-top: 1.9rem;
      p {
        width: 100%;
        border: 1px solid #979797;
        border-radius: 5px;
        height: 3.2rem;
        line-height: 3.2rem;
        padding-left: 0.7rem;
        margin-bottom: 1.25rem;
      }
      .active {
        color: #fff;
        background-color: #f1727c;
        border: 0;
      }
      .actives {
        color: #fff;
        background-color: #f1727c;
        border: 0;
      }
      .success {
        color: #00862a;
        background-color: #fff;
        border: 1px solid #00862a;
        position: relative;
      }
      .success::before {
        content: "";
        background: url("../assets/image/success_icon.png") no-repeat;
        position: absolute;
        display: inline-block;
        height: 1rem;
        width: 1rem;
        top: 57%;
        transform: translateY(-50%);
        background-color: #fff;
        padding: 0.1875rem;
      }
      .error {
        border: 1px solid #d81e06;
        color: red;
        background-color: #fff;
        position: relative;
      }
      .error::before {
        content: "";
        background: url("../assets/image/error_icon.png") no-repeat;
        position: absolute;
        display: inline-block;
        height: 1rem;
        width: 1rem;
        top: 57%;
        transform: translateY(-50%);
        background-color: #fff;
        padding: 0.1875rem;
      }
    }
  }

  .bom_btn {
    display: block;
    height: 3.2rem;
    width: 80%;
    border-radius: 5px;
    color: #fff;
    font-size: 20px;
    line-height: 50px;
    text-align: center;
    background: #d42454;
    margin: 2.8rem auto 0rem;
  }
  .disabled {
    background-color: #efafc0;
  }
}
</style>
