<template>
  <div class="multiple">
    <div class="content">
      <div class="title_text">
        <div class="title">多选题</div>
        <div class="num">
          <span>{{ active + 1 }}</span
          >/{{ mulList.length }}
        </div>
      </div>
      <div>1.回复12345政务热线工单的处理结果包含以下几项内容_____。(多选题)</div>
      <div class="choice">
        <p
          v-for="(item, index) in mulList[active].arr"
          :key="index"
          @click="AddCLick(item)"
          :class="{ active: item.checked }"
        >
          {{ item.name }}
        </p>
      </div>
    </div>
    <div class="bom_btn" :class="{ disabled: isnext }" @click="NextClick" v-show="!show">
      下一题
    </div>
    <div v-show="show">
      <div class="bg_log"></div>
      <div class="log_text">
        <div class="title">很遗憾，您答错了！</div>
        <div class="text">
          <p class="one_p">正确答案为DE</p>
          <div class="one_resu">
            <p>D、回访用户时间</p>
            <p>E、用户意见反馈</p>
          </div>
          <div class="two_retu" @click="show = false">返回</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      active: 0,
      show: false,
      mulList: [
        {
          name: "多选题",
          arr: [
            { name: "A、退款金额", checked: false },
            { name: "B、用户满意度", checked: false },
            { name: "C、故障修复报告", checked: false },
            { name: "D、回访用户时间", checked: false },
            { name: "E、用户意见反馈", checked: false },
          ],
        },
        {
          name: "多选题",
          arr: [
            { name: "A、退款金额", checked: false },
            { name: "B、用户满意度", checked: false },
            { name: "C、故障修复报告", checked: false },
            { name: "D、回访用户时间", checked: false },
            { name: "E、用户意见反馈", checked: false },
          ],
        },
      ],
    };
  },
  computed: {
    isnext() {
      return !this.mulList[this.active].arr.filter((item) => item.checked).length;
    },
  },
  methods: {
    AddCLick(item) {
      item.checked = !item.checked;
    },
    NextClick() {
      if (this.isnext) {
        return false;
      }
      this.show = true;
    },
  },
};
</script>
<style lang="scss" scoped>
