  .content {
    margin: 0 auto;
    width: 90%;
    border-radius: 5px;
    background-color: #fff;
    padding: 1rem;
    .title_text {
      display: flex;
      justify-content: space-between;
      .title {
        background-image: url("../assets/image/title_icon.png");
        width: 122px;
        height: 2.7rem;
        background-position: center;
        background-size: cover;
        color: #fff;
        text-align: center;
        line-height: 2.7rem;
        position: relative;
        top: -1.7rem;
        left: -1rem;
      }
    }
    .choice {
      margin-top: 1.9rem;
      p {
        width: 100%;
        border: 1px solid #979797;
        border-radius: 5px;
        height: 3.2rem;
        line-height: 3.2rem;
        padding-left: 0.7rem;
      }
      p:not(:last-child) {
        margin-bottom: 20px;
      }
      .active {
        color: #fff;
        background-color: #f1727c;
        border: 0;
      }
    }
  }
  .bom_btn {
    display: block;
