  .bg_log::before {
    content: "";
    width: 100%;
    height: 100%;
    background-color: #979797;
    opacity: 0.6;
    top: 0;
    left: 0;
    position: absolute;
  }
  .log_text {
    position: absolute;
    top: 51%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    height: 22.25rem;
    width: 80%;
    border-radius: 0.3125rem;
    .title {
      padding: 1.875rem 3.5625rem 1.5625rem;
      border-bottom: 2px solid #ececec;
      font-size: 1.25rem;
      color: #f1727c;
    }
    .text {
      text-align: center;
      padding-top: 1.5625rem;
      .one_p {
        font-size: 1.25rem;
        color: #00862a;
        padding-bottom: 1.6875rem;
      }
      .one_resu {
        margin-left: -6.25rem;
        color: #333333;
      }
      .two_retu {
        height: 3.125rem;
        width: 9.375rem;
        display: inline-block;
        text-align: center;
        line-height: 3.125rem;
        color: #fff;
        font-size: 1.25rem;
        background: #d42454;
        border-radius: 5px;
        margin-top: 3.3125rem;
      }
    }
  }
}
</style>
